body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
	overflow-y: auto;
}

.hero-body button {
	margin-top: 2rem;
}

button.is-loading .is-hidden-when-loading {
	opacity: 0;
}

.has-padding-2 {
	padding: 1rem;
}

.has-padding-2-bottom {
	padding-bottom: 1rem;
}

.graph-container {
	height: 80vh;
	align-items: center;
	justify-content: center;
}

.graph-component {
	width: 100%;
	height: 100%;
}

.justify-center {
	justify-content: center;
}

.hero-footer {
  padding-bottom: 1rem;
}

@media screen and (min-width: 1281px) {
  .graph-component {
    width: 75%;
  }
}
